import {ColorPalette, PanelBody, RangeControl, SelectControl, TextControl} from "@wordpress/components";
import {useEntityProp} from '@wordpress/core-data';

const {__} = wp.i18n;

function GeneralSettings() {
    const [meta, setMeta] = useEntityProp('postType', 'qr', 'meta');

    const backgroundColors = [
        {name: 'qyrr', color: '#6804cc'},
        {name: 'white', color: '#fff'},
        {name: 'black', color: '#000'},
    ];

    const fillColors = [
        {name: 'qyrr', color: '#6804cc'},
        {name: 'white', color: '#fff'},
        {name: 'black', color: '#000'},
    ];

    return (
        <PanelBody title={__('General Settings', 'qyrr-code')} initialOpen={false}>
            <TextControl
                label={__('Size (in px)', 'qyrr-code')}
                value={meta['size']}
                onChange={(value) => setMeta({...meta, size: value})}
                type={"number"}
            />
            <label className={"color-palette-label"}>{__('Background Color', 'qyrr-code')}</label>
            <ColorPalette
                colors={backgroundColors}
                value={meta['background-color']}
                onChange={(value) => {
                    if (undefined === value) {
                        setMeta({...meta, "background-color": ""})
                    } else {
                        setMeta({...meta, "background-color": value})
                    }
                }
                }
            />
            <label className={"color-palette-label"}>{__('Fill Color', 'qyrr-code')}</label>
            <ColorPalette
                colors={fillColors}
                value={meta['fill-color']}
                onChange={(value) => {
                    if (undefined === value) {
                        setMeta({...meta, "fill-color": ""})
                    } else {
                        setMeta({...meta, "fill-color": value})
                    }
                }
                }
            />
            <TextControl
                label={__('Min Version', 'qyrr-code')}
                value={meta['min-version']}
                onChange={(value) => setMeta({...meta, "min-version": value})}
                type={"number"}
            />
            <RangeControl
                label={__('Quiet Zone', 'qyrr-code')}
                value={meta['quiet-zone']}
                onChange={(value) => setMeta({...meta, "quiet-zone": value})}
                min={0}
                max={4}
            />
            <RangeControl
                label={__('Corner Radius', 'qyrr-code')}
                value={meta['corner-radius']}
                onChange={(value) => setMeta({...meta, "corner-radius": value})}
                min={0}
                max={50}
            />
            <SelectControl
                label={__('Error Handling Level', 'qyrr-code')}
                value={meta['error-handling-level']}
                options={[
                    {label: 'L - Low (7%)', value: 'L'},
                    {label: 'M - Medium (15%)', value: 'M'},
                    {label: 'Q - Quartile (25%)', value: 'Q'},
                    {label: ' H - High (30%)', value: 'H'},
                ]}
                onChange={(value) => setMeta({...meta, "error-handling-level": value})}
            />
        </PanelBody>
    );
}

export default GeneralSettings;