import {InspectorControls, useBlockProps} from '@wordpress/block-editor';
import {useEntityProp} from '@wordpress/core-data';
import './editor.scss';
import SourceSettings from './components/SourceSettings';
import GeneralSettings from './components/GeneralSettings';
import LabelSettings from './components/LabelSettings';
import DownloadSettings from './components/DownloadSettings';
import ShortcodeSettings from './components/ShortcodeSettings';
import TemplateSettings from './components/TemplateSettings';
import BulkGeneratorSettings from './components/BulkGeneratorSettings';

import {Kjua} from 'react-kjua';
import {useSelect} from "@wordpress/data";
import {useEffect, useState} from "@wordpress/element";
import apiFetch from "@wordpress/api-fetch";

const {__} = wp.i18n;


export default function Edit({setAttributes, noticeOperations, noticeUI}) {
    const [meta] = useEntityProp('postType', 'qr', 'meta');
    const [isBlockSelected, setIsBlockSelected] = useState(false);
    const buffer = new Image();
    buffer.src = meta['logo-upload'];

    const selectedBlock = useSelect(
        (select) => select('core/block-editor').getSelectedBlock()
    );


    const postID = useSelect(
        (select) => {
            const {getCurrentPostId} = select('core/editor');
            return getCurrentPostId();
        },
        []
    );
    const saveFile = (type, source, isPreview) => {
        if ('svg' === type) {
            let reader = new FileReader();

            reader.readAsDataURL(source);
            reader.onloadend = function () {
                apiFetch({
                    path: '/qyrr/v1/blob-to-file',
                    method: 'POST',
                    data: {
                        post_id: postID,
                        format: type,
                        source: reader.result,
                    },
                });
            };
        } else {
            apiFetch({
                path: '/qyrr/v1/blob-to-file',
                method: 'POST',
                data: {
                    post_id: postID,
                    format: type,
                    source: source,
                },
            });
        }
    }

    const saveFilesToDisk = () => {
        // Original image.
        let container = document.getElementById('qr-code-container');
        let inner_div = container.firstElementChild;
        let qr_image = inner_div.firstElementChild;

        if (qr_image.src) {
            saveFile('png', qr_image.src, false);
        } else if (qr_image.nodeName === 'CANVAS') {
            // Canvas - render as image file.
            qr_image.toBlob(function (blob) {
                saveFile('svg', blob, false);
            });
        } else if (qr_image.innerHTML) {
            // Render as SVG file.
            let svg = '<svg title="graph" xmlns="http://www.w3.org/2000/svg">' + qr_image.innerHTML + '</svg>';
            let blob = new Blob([svg], {type: "image/svg+xml"});

            saveFile('svg', blob, false);
        }
    }

    useEffect(() => {
        if (selectedBlock) {
            setIsBlockSelected(true);
        }

        saveFilesToDisk();
    }, [selectedBlock]);

    return (
        <div {...useBlockProps()}>
            <div className={"qyrr-container-wrapper"}>
                <InspectorControls>
                    <SourceSettings/>
                    <GeneralSettings/>
                    <LabelSettings noticeOperations={noticeOperations} noticeUI={noticeUI}
                                   setAttributes={setAttributes}/>
                    <DownloadSettings/>
                    <ShortcodeSettings/>
                    {license.is_pro &&
                        <>
                            <TemplateSettings/>
                            <BulkGeneratorSettings/>
                        </>
                    }
                </InspectorControls>
                {!isBlockSelected &&
                    <div style={{textAlign: 'center', color: '#6804cc'}}>
                        <p>{__('Click on the QR Code to customize it.', 'qyrr-code')}</p>
                    </div>
                }
                <div id={"qr-code-container"} className={"apply-font"}>
                    <Kjua
                        render={meta['download_format']}
                        text={meta['qr-content']}
                        image={buffer}
                        minVersion={meta['min-version']}
                        ecLevel={meta['error-handling-level']}
                        size={meta['size']}
                        fill={meta['fill-color']}
                        back={meta['background-color']}
                        rounded={meta['corner-radius']}
                        quiet={meta['quiet-zone']}
                        mode={meta['render-mode']}
                        mSize={meta['logo-size']}
                        mPosX={meta['position-x']}
                        mPosY={meta['position-y']}
                        label={meta['label-text']}
                        fontname={meta['font']}
                        fontcolor={meta['font-color']}
                    />
                </div>
            </div>
        </div>
    );
}