import {useEntityProp} from '@wordpress/core-data';
import {MediaPlaceholder, MediaReplaceFlow} from '@wordpress/block-editor';
import {Button} from "@wordpress/components";

const {__} = wp.i18n;


function ImageUploader({noticeOperations, noticeUI}) {
    const [meta, setMeta] = useEntityProp('postType', 'qr', 'meta');


    const onSelectImage = (image) => {
        if (!image || !image.url) {
            setMeta({...meta, "logo-upload": ''});
            return;
        }

        setMeta({
            ...meta,
            "logo-upload": image.url,
            'render-mode': 'image',
            "error-handling-level": "H",
            "min-version": 5,
            "logo-max-size": 40,
        });
    };

    const onUploadError = (message) => {
        noticeOperations.removeAllNotices();
        noticeOperations.createErrorNotice(message);
    };

    const removeImage = () => {
        setMeta({
            'logo-upload': null,
            'render-mode': 'plain',
        });
    };

    return (
        <div>

            {meta['logo-upload'] ?
                <div>
                    <div className={"image-placeholder"}>
                        <img src={meta['logo-upload']}/>
                    </div>
                    <MediaReplaceFlow
                        name={__('Replace Image', 'qyrr-code')}
                        onSelect={onSelectImage}
                        onError={onUploadError}
                        accept="image/*"
                        allowedTypes={['image']}
                        mediaURL={meta['logo-upload']}
                    />
                    <Button variant="secondary" onClick={removeImage}>
                        {__('Remove Image', 'qyrr-code')}
                    </Button>
                </div>
                :
                <MediaPlaceholder
                    icon="format-image"
                    onSelect={onSelectImage}
                    onError={onUploadError}
                    accept="image/*"
                    allowedTypes={['image']}
                    notices={noticeUI}
                />
            }
        </div>
    )
}

export default ImageUploader;