import {PanelBody, ClipboardButton} from "@wordpress/components";
import {useState} from "@wordpress/element";
import {useSelect} from "@wordpress/data";

const {__} = wp.i18n;

function ShortcodeSettings() {
    const [hasCopied, setHasCopied] = useState(false);

    const postID = useSelect(
        (select) => {
            const {getCurrentPostId} = select('core/editor');
            return getCurrentPostId();
        },
        []
    );

    return (
        <PanelBody title={__('Shortcode', 'qyrr-code')} initialOpen={false}>
            {postID &&
                <>
                    <h4>{__('Copy the Shortcode', 'qyrr-code')}</h4>
                    <ClipboardButton
                        variant="secondary"
                        text={`[qyrr code="${postID}"]`}
                        onCopy={() => setHasCopied(true)}
                        onFinishCopy={() => setHasCopied(false)}
                    >
                        {hasCopied ? __('Copied!', 'qyrr-code') : `[qyrr code="${postID}"]`}
                    </ClipboardButton>
                </>
            }
        </PanelBody>
    );
}

export default ShortcodeSettings;

