import {PanelBody, SelectControl, TextareaControl, TextControl, ToggleControl} from "@wordpress/components";
import {useEntityProp} from '@wordpress/core-data';
import {useSelect} from "@wordpress/data";
import {useEffect, useState} from "@wordpress/element";
import apiFetch from "@wordpress/api-fetch";

const {__} = wp.i18n;

function SourceSettings() {
    const [meta, setMeta] = useEntityProp('postType', 'qr', 'meta');
    const [postTypes, setPostTypes] = useState([]);
    const [isDynamicUrl, setIsDynamicUrl] = useState(false);
    const [isDynamicPost, setIsDynamicPost] = useState(false);

    const getPostTypes = () => {
        if (license.is_pro) {
            apiFetch({
                path: '/qyrr/v1/selectable-post-types',
                method: 'GET',
            }).then((res) => {
                setPostTypes(res);
            });
        } else {
            setPostTypes([
                {label: __('Post', 'qyrr-code'), value: 'post'},
                {label: __('Page', 'qyrr-code'), value: 'page'},
            ]);
        }
    }

    const posts = useSelect(
        (select) => {
            const {getEntityRecords} = select('core');
            return getEntityRecords('postType', meta['post-type'], {
                per_page: -1,
                order: 'asc',
                status: 'publish'
            });
        },
        [meta]
    );

    const postId = useSelect(
        (select) => {
            const {getCurrentPostId} = select('core/editor');
            return getCurrentPostId();
        },
        []
    );


    const getSelectablePosts = () => {
        if (!posts) {
            return [];
        }

        const options = [];

        posts.map(function (post) {
            if (post.title.raw && post.title.raw !== '') {
                options.push({
                    label: post.title.raw,
                    value: post.link,
                });

            }
            return post;
        });

        return options;
    };

    useEffect(() => {
        getPostTypes();

        if (meta['source'] === '') {
            setMeta({"source": 'post'});
        }

        if (meta['dynamic-url']) {
            setIsDynamicUrl(true);
        }

        if (meta['dynamic-post']) {
            setIsDynamicPost(true);
        }

    }, [meta]);

    const sourceOptions = () => {
        if (license.is_pro) {
            return [
                {label: __('Post/Page/CPT', 'qyrr-code'), value: 'post'},
                {label: __('External URL', 'qyrr-code'), value: 'url'},
                {label: __('Text', 'qyrr-code'), value: 'text'},
                {label: __('E-Mail', 'qyrr-code'), value: 'e-mail'},
                {label: __('Phone', 'qyrr-code'), value: 'phone'},
                {label: __('SMS', 'qyrr-code'), value: 'sms'},
                {label: __('WhatsApp', 'qyrr-code'), value: 'whatsapp'},
                {label: __('GEO URI (Location)', 'qyrr-code'), value: 'location'},
                {label: __('WiFi', 'qyrr-code'), value: 'wifi'},
                {label: __('vCard', 'qyrr-code'), value: 'vcard'},
            ];
        } else {
            return [
                {label: __('Post/Page', 'qyrr-code'), value: 'post'},
                {label: __('External URL', 'qyrr-code'), value: 'url'},
            ]
        }
    }

    return (
        <PanelBody title={__('Source', 'qyrr-code')} initialOpen={true}>
            <SelectControl
                label={__('Type', 'qyrr-code')}
                value={meta['source']}
                options={sourceOptions()}
                onChange={(value) => setMeta({...meta, "source": value})}
            />
            {meta['source'] === 'post' &&
                <>
                    {license.is_pro &&
                        <ToggleControl
                            label={__('Dynamic QR-Code', 'qyrr-code')}
                            help={
                                isDynamicPost
                                    ? __('Use a dynamic QR code where the source can be changed without changing the QR-Code. This also enables tracking.', 'qyrr-code')
                                    : __('Use a traditional QR code where the source can not be changed without changing the QR-Code.', 'qyrr-code')
                            }
                            checked={isDynamicPost}
                            onChange={(value) => {
                                setMeta({...meta, "dynamic-post": value});
                                setIsDynamicPost(value);
                            }
                            }
                        />
                    }
                    <SelectControl
                        label={__('Select Post-Type', 'qyrr-code')}
                        value={meta['post-type']}
                        options={postTypes}
                        onChange={(value) => {
                            setMeta({...meta, "post-type": value})
                        }
                        }
                    />
                    <SelectControl
                        label={__('Select Post', 'qyrr-code')}
                        value={meta['post-link']}
                        options={getSelectablePosts()}
                        onChange={(value) => {
                            if (isDynamicPost) {
                                setMeta({
                                    ...meta,
                                    "qr-content": license.home_url + '?qyrr=' + postId,
                                    "post-link": value,
                                })
                            } else {
                                setMeta({
                                    ...meta,
                                    "qr-content": value,
                                    "post-link": value,
                                })
                            }
                        }
                        }
                    />
                </>
            }
            {
                meta['source'] === 'url' &&
                <>
                    {license.is_pro &&
                        <ToggleControl
                            label={__('Dynamic QR-Code', 'qyrr-code')}
                            help={
                                isDynamicUrl
                                    ? __('Use a dynamic QR code where the source can be changed without changing the QR-Code. This also enables tracking.', 'qyrr-code')
                                    : __('Use a traditional QR code where the source can not be changed without changing the QR-Code.', 'qyrr-code')
                            }
                            checked={isDynamicUrl}
                            onChange={(value) => {
                                setMeta({...meta, "dynamic-url": value});
                                setIsDynamicUrl(value);
                            }
                            }
                        />
                    }
                    <TextControl
                        label={__('External URL', 'qyrr-code')}
                        value={meta['url']}
                        placeholder={"https://yourwebsite.com"}
                        onChange={(value) => {
                            if (isDynamicUrl) {
                                setMeta({
                                    ...meta,
                                    "qr-content": license.home_url + '?qyrr=' + postId,
                                    "url": value,
                                })
                            } else {
                                setMeta({
                                    ...meta,
                                    "qr-content": value,
                                    "url": value,
                                })
                            }
                        }
                        }
                        type={"url"}
                    />
                </>
            }
            {license.is_pro &&
                <>
                    {meta['source'] === 'text' &&
                        <TextareaControl
                            label={__('Text', 'qyrr-code')}
                            placeholder={"Add your text here.."}
                            value={meta['text']}
                            onChange={(value) => setMeta({
                                ...meta,
                                "qr-content": value,
                                "text": value,
                            })}
                        />
                    }
                    {meta['source'] === 'e-mail' &&
                        <TextControl
                            label={__('E-Mail', 'qyrr-code')}
                            value={meta['e-mail']}
                            placeholder={"hello@test.com"}
                            onChange={(value) => setMeta({
                                ...meta,
                                "qr-content": 'mailto:' + value,
                                "e-mail": value,
                            })}
                            type={"text"}
                        />
                    }
                    {meta['source'] === 'phone' &&
                        <TextControl
                            label={__('Phone', 'qyrr-code')}
                            value={meta['phone-number']}
                            placeholder={"00123456"}
                            onChange={(value) => setMeta({
                                ...meta,
                                "qr-content": 'tel:' + value,
                                "phone-number": value,
                            })}
                            type={"phone"}
                        />
                    }
                    {meta['source'] === 'sms' &&
                        <>
                            <TextControl
                                label={__('Phone Number', 'qyrr-code')}
                                value={meta['phone-number']}
                                placeholder={"00123456"}
                                onChange={(value) => setMeta({...meta, "phone-number": value})}
                                type={"phone"}
                            />
                            <TextareaControl
                                label={__('SMS', 'qyrr-code')}
                                placeholder={"Add your SMS text here.."}
                                value={meta['sms-text']}
                                onChange={(value) => setMeta({
                                    ...meta,
                                    "qr-content": 'sms:' + meta['phone-number'] + '&body=' + encodeURIComponent(value),
                                    "sms-text": value,
                                })}
                            />
                        </>
                    }
                    {meta['source'] === 'whatsapp' &&
                        <>
                            <TextControl
                                label={__('WhatsApp Number', 'qyrr-code')}
                                value={meta['phone-number']}
                                placeholder={"00123456"}
                                onChange={(value) => setMeta({...meta, "phone-number": value})}
                                type={"phone"}
                            />
                            <TextareaControl
                                label={__('Message', 'qyrr-code')}
                                placeholder={"Add your Whatsapp message here.."}
                                value={meta['whatsapp-text']}
                                onChange={(value) => setMeta({
                                    ...meta,
                                    "qr-content": 'https://wa.me/' + meta['phone-number'] + '?text=' + encodeURIComponent(value),
                                    "whatsapp-text": value,
                                })}
                            />
                        </>
                    }
                    {meta['source'] === 'location' &&
                        <>
                            <TextControl
                                label={__('Latitude', 'qyrr-code')}
                                value={meta['latitude']}
                                placeholder={"37.786971"}
                                onChange={(value) => setMeta({...meta, "latitude": value})}
                                type={"text"}
                            />
                            <TextControl
                                label={__('Longitude', 'qyrr-code')}
                                value={meta['longitude']}
                                placeholder={"-122.399677"}
                                onChange={(value) => setMeta({
                                    ...meta,
                                    "longitude": value,
                                    "qr-content": 'geo:' + meta['latitude'] + ',' + value,
                                })}
                                type={"text"}
                            />
                        </>
                    }

                    {meta['source'] === 'wifi' &&
                        <>
                            <SelectControl
                                label={__('Select WiFi Type', 'qyrr-code')}
                                value={meta['wifi-type']}
                                options={[
                                    {label: 'WPA', value: 'wpa'},
                                    {label: 'WEP', value: 'wep'},
                                    {label: 'nyanpass', value: 'nyanpass'}
                                ]}
                                onChange={(value) => setMeta({...meta, "wifi-type": value})}
                            />
                            <TextControl
                                label={__('SSID', 'qyrr-code')}
                                value={meta['ssid']}
                                onChange={(value) => setMeta({...meta, "ssid": value})}
                                type={"text"}
                            />
                            <TextControl
                                label={__('Password', 'qyrr-code')}
                                value={meta['password']}
                                onChange={(value) => {
                                    let wifi_link;

                                    switch (meta['wifi-type']) {
                                        case 'wpa':
                                            wifi_link = 'WIFI:T:WPA;S:' + meta['ssid'] + ';P:' + value + ';;'
                                            break;
                                        case 'wpe':
                                            wifi_link = 'WIFI:T:WEP;S:' + meta['ssid'] + ';P:' + value + ';;'
                                            break;
                                        case 'nyanpass':
                                            wifi_link = 'WIFI:T:nopass;S:' + meta['ssid'] + ';P:' + value + ';;'
                                            break;
                                        default:
                                            wifi_link = 'WIFI:T:WPA;S:' + meta['ssid'] + ';P:' + value + ';;'
                                    }

                                    setMeta({
                                        ...meta,
                                        "password": value,
                                        "qr-content": wifi_link,
                                    })
                                }}
                                type={"text"}
                            />
                        </>
                    }
                    {meta['source'] === 'vcard' &&
                        <>
                            <TextControl
                                label={__('First name', 'qyrr-code')}
                                value={meta['vcard-first-name']}
                                onChange={(value) => setMeta({...meta, "vcard-first-name": value})}
                                type={"text"}
                            />
                            <TextControl
                                label={__('Last name', 'qyrr-code')}
                                value={meta['vcard-last-name']}
                                onChange={(value) => setMeta({...meta, "vcard-last-name": value})}
                                type={"text"}
                            />
                            <TextControl
                                label={__('Company', 'qyrr-code')}
                                value={meta['vcard-company']}
                                onChange={(value) => setMeta({...meta, "vcard-company": value})}
                                type={"text"}
                            />
                            <TextControl
                                label={__('Address', 'qyrr-code')}
                                value={meta['vcard-address']}
                                placeholder={"Street;City;Zipcode"}
                                onChange={(value) => setMeta({...meta, "vcard-address": value})}
                                type={"text"}
                            />
                            <TextControl
                                label={__('Phone', 'qyrr-code')}
                                value={meta['vcard-phone']}
                                placeholder={"+49 999 12345"}
                                onChange={(value) => setMeta({...meta, "vcard-phone": value})}
                                type={"text"}
                            />
                            <TextControl
                                label={__('URL', 'qyrr-code')}
                                value={meta['vcard-url']}
                                onChange={(value) => setMeta({...meta, "vcard-url": value})}
                                type={"url"}
                            />
                            <TextControl
                                label={__('E-Mail', 'qyrr-code')}
                                value={meta['vcard-email']}
                                onChange={(value) => {
                                    let vcard = "BEGIN:VCARD\nVERSION:3.0\nN:" + meta['vcard-last-name'] + ";" + meta['vcard-first-name'] + "\nFN:" + meta['vcard-first-name'] + " " + meta['vcard-last-name'] + "\nORG:" + meta['vcard-company'] + "\nADR;WORK:;;" + meta['vcard-address'] + "\nTEL;WORK;VOICE:" + meta['vcard-phone'] + "\nURL:" + meta['vcard-url'] + "\nEMAIL;INTERNET:" + value + "\nEND:VCARD";

                                    setMeta({
                                        ...meta,
                                        "vcard-email": value,
                                        "qr-content": vcard
                                    })
                                }
                                }
                                type={"text"}
                            />
                        </>
                    }
                </>
            }
        </PanelBody>
    );
}

export default SourceSettings;