import {PanelBody, ToggleControl, Button, Notice, Animate} from "@wordpress/components";
import {useState} from "@wordpress/element";
import {useSelect} from "@wordpress/data";
import {useEntityProp} from "@wordpress/core-data";

const {__} = wp.i18n;

function TemplateSettings() {
    const [meta, setMeta] = useEntityProp('postType', 'qr', 'meta');
    const [isTemplate, setIsTemplate] = useState(meta['template']);
    const [globalTemplateLoaded, setGlobalTemplateLoaded] = useState(false);

    const postID = useSelect(
        (select) => {
            const {getCurrentPostId} = select('core/editor');
            return getCurrentPostId();
        },
        []
    );

    const loadGlobalTemplateData = () => {
        setGlobalTemplateLoaded(true);
        setMeta(license.template_meta);

        setTimeout(function () {
            setGlobalTemplateLoaded(false);
        }, 2000);
    }
    return (
        <PanelBody title={__('Template', 'qyrr-code')} initialOpen={false}>
            {postID &&
                <>
                    <ToggleControl
                        label={__('Save as Template', 'qyrr-code')}
                        help={
                            isTemplate
                                ? __('Saves the current QR code as a template. You can select a global template for new QR codes in Qyrr->Settings->Global Template.', 'qyrr-code')
                                : ''
                        }
                        checked={isTemplate}
                        onChange={(value) => {
                            setMeta({...meta, "template": value});
                            setIsTemplate(value);
                        }
                        }
                    />
                    <Button onClick={loadGlobalTemplateData}
                            variant="secondary">{__('Load from Global Template', 'qyrr-code')}</Button>
                    {globalTemplateLoaded &&
                        <Animate type="slide-in" options={{origin: 'top'}}>
                            {() => (
                                <Notice status="success" isDismissible={false}>
                                    {__('Global Template styles successfully imported.', 'qyrr-code')}
                                </Notice>
                            )}
                        </Animate>
                    }
                </>
            }
        </PanelBody>
    );
}

export default TemplateSettings;

