import {ColorPalette, PanelBody, RangeControl, SelectControl, TextControl} from "@wordpress/components";
import {useEntityProp} from '@wordpress/core-data';
import FontPicker from "@olegstankoptev/font-picker-react"
import ImageUploader from "./partials/ImageUploader";
import {useEffect, useState} from "@wordpress/element";

const {__} = wp.i18n;

function LabelSettings({noticeOperations, noticeUI}) {
    const [meta, setMeta] = useEntityProp('postType', 'qr', 'meta');
    const [googleApiKey, setGoogleApiKey] = useState('');

    const fontColors = [
        {name: 'qyrr', color: '#6804cc'},
        {name: 'white', color: '#fff'},
        {name: 'black', color: '#000'},
    ];

    const renderModes = () => {

        if ('' !== meta['logo-upload']) {
            return [
                {label: __('Plain', 'qyrr-code'), value: 'plain'},
                {label: __('Label', 'qyrr-code'), value: 'label'},
                {label: __('Logo', 'qyrr-code'), value: 'image'},
            ]
        } else if ('' !== meta['label-text']) {
            return [
                {label: __('Plain', 'qyrr-code'), value: 'plain'},
                {label: __('Label', 'qyrr-code'), value: 'label'},
            ]
        } else {
            return [
                {label: __('Plain', 'qyrr-code'), value: 'plain'},
            ]
        }

    }

    useEffect(() => {
        if ('' !== license.google_fonts_api_key) {
            setGoogleApiKey(license.google_fonts_api_key);
        }
    }, []);

    return (
        <PanelBody title={__('Logo or Label', 'qyrr-code')} initialOpen={false}>
            <TextControl
                label={__('Label Text', 'qyrr-code')}
                value={meta['label-text']}
                onChange={(value) => setMeta({...meta, "label-text": value})}
                type={"text"}
            />
            <label className={"color-palette-label"}>{__('Logo Upload', 'qyrr-code')}</label>
            <div className={"logo-uploader"}>
                <ImageUploader noticeOperations={noticeOperations} noticeUI={noticeUI}/>
            </div>
            <SelectControl
                label={__('Render Mode', 'qyrr-code')}
                value={meta['render-mode']}
                options={renderModes()}
                onChange={(value) => {
                    if ('image' === value) {
                        setMeta({
                            ...meta,
                            "error-handling-level": "H",
                            "min-version": 5,
                            "render-mode": value,
                            "logo-max-size": 40,
                        })
                    } else {
                        setMeta({
                            ...meta,
                            "render-mode": value,
                            "logo-max-size": 100,
                        })
                    }
                }
                }
            />
            <RangeControl
                label={__('Label / Logo Size (%)', 'qyrr-code')}
                value={meta['logo-size']}
                onChange={(value) => setMeta({...meta, "logo-size": value})}
                initialPosition={30}
                min={10}
                max={meta['logo-max-size']}
            />
            <RangeControl
                label={__('Position X (%)', 'qyrr-code')}
                value={meta['position-x']}
                onChange={(value) => setMeta({...meta, "position-x": value})}
                initialPosition={50}
                min={10}
                max={100}
            />
            <RangeControl
                label={__('Position Y (%)', 'qyrr-code')}
                value={meta['position-y']}
                onChange={(value) => setMeta({...meta, "position-y": value})}
                initialPosition={50}
                min={10}
                max={100}
            />
            <label className={"color-palette-label"}>{__('Select Font', 'qyrr-code')}</label>
            {'' !== googleApiKey ?
                <>
                    {typeof meta['font'] === 'string' && meta['font'].length === 0 ?
                        <FontPicker
                            apiKey={googleApiKey}
                            activeFontFamily="sans"
                            onChange={(value) => setMeta({...meta, "font": value.family})}
                        />
                        :
                        <FontPicker
                            apiKey={googleApiKey}
                            activeFontFamily={meta['font']}
                            onChange={(value) => setMeta({...meta, "font": value.family})}
                        />
                    }
                </>
                :
                <>
                    <p>
                        <small>
                            {__('You need to configure your Google Fonts API key in the settings to use other fonts.', 'qyrr-code')}
                        </small>
                    </p>
                </>
            }
            <label className={"color-palette-label"}>{__('Font Color', 'qyrr-code')}</label>
            <ColorPalette
                colors={fontColors}
                value={meta['font-color']}
                onChange={(value) => setMeta({...meta, "font-color": value})}
            />
        </PanelBody>
    );
}

export default LabelSettings;