import {Button, PanelBody, SelectControl} from "@wordpress/components";
import {useEntityProp} from "@wordpress/core-data";
import {saveAs} from 'file-saver';
import {useSelect} from "@wordpress/data";
import apiFetch from "@wordpress/api-fetch";
import {useEffect, useState} from "@wordpress/element";

const {__} = wp.i18n;

function DownloadSettings() {
    const [meta, setMeta] = useEntityProp('postType', 'qr', 'meta');
    const [fileName, setFileName] = useState('qr-code');

    const postID = useSelect(
        (select) => {
            const {getCurrentPostId} = select('core/editor');
            return getCurrentPostId();
        },
        []
    );

    const handleDownload = () => {
        let container = document.getElementById('qr-code-container');
        let inner_div = container.firstElementChild;
        let image = inner_div.firstElementChild;

        if (image.src) {
            // Download PNG.
            saveAs(image.src, fileName + ".png");
        } else if (image.nodeName === 'CANVAS') {
            // Canvas - render as image file.
            image.toBlob(function (blob) {
                saveFile('svg', blob, false);
            });
        } else if (image.innerHTML) {
            // Render as SVG file.
            let svg = '<svg title="graph" version="1.1" xmlns="http://www.w3.org/2000/svg">' + image.innerHTML + '</svg>';
            let blob = new Blob([svg], {type: "image/svg+xml"});

            // Download SVG.
            saveAs(blob, fileName + ".svg");
        }
    }

    const formatOptions = () => {
        if (license.is_pro) {
            return [
                {label: 'PNG', value: 'image'},
                {label: 'SVG', value: 'svg'},
            ];
        } else {
            return [
                {label: 'PNG', value: 'image'}
            ]
        }
    }

    const getFileName = (postID) => {
        // Get file name.
        apiFetch({
            path: '/qyrr/v1/file-name',
            method: 'POST',
            data: {
                post_id: postID,
            },
        }).then((file_name) => {
            setFileName(file_name);
        });
    }

    useEffect(() => {
        getFileName(postID);
    }, [postID]);


    return (
        <PanelBody title={__('Download', 'qyrr-code')} initialOpen={false}>
            <SelectControl
                label={__('Download Format', 'qyrr-code')}
                value={meta['download_format']}
                options={formatOptions()}
                onChange={(value) => {
                    setMeta({...meta, "download_format": value});
                }}
            />
            <Button variant="primary" id="download-qr-code"
                    onClick={handleDownload}>{__('Download', 'qyrr-code')}</Button>
        </PanelBody>
    );
}

export default DownloadSettings;

